.product-card{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 300px;
    /* border: 5px solid #2cbc19; */
    border-radius: 5px;
    background-color: #fff;
    box-shadow: 1px 1px 10px -5px #000000;
    padding: 20px;
    margin: auto;
}

.product-price-and-add-to-cart{
    font-size: calc(10px + 2vmin);
    display: flex;
    justify-content: space-around;
    flex-direction: row;
}

.product-img{
    /* background-color: #ccc; */
    aspect-ratio: 1;
    height: 200px;
}

.product-img img{
    object-fit: cover;
    max-width: 100%;
    max-height: 100%;
}

.product-details{
    min-width: 100%;
}

.product-details span{
    color: #a3a3a3;
}

.product-details-title{
    /* background-color: #2cbc19; */
    font-weight: 400;
    text-align: center;
    color: #a3a3a3;
    font-size: calc(1px + 2vmin);    
    box-shadow: 1px 1px 5px 0px #ffffff;
    cursor:pointer;
    /* background-color: #9d9c9c3d ; */
}

.display-none{
    display: none;
}