.cart{
    position: absolute;
    width: 50vw;
    height: 90vh;
    right: 0;
    z-index: 1;
    background-color: white;
    overflow-y: scroll;
}

.conclude-cart{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.cart-close-icon {
    position: relative;
    z-index: 2;
    right: -20vw;
    top:6vh;
}

.cart-icon{
    width: 30px;
    height: 30px;
    margin: 20px 15px 0 0;
}

.visible{
    visibility: visible;
}

.hidden{
    visibility: hidden;
}

.total-price-container{
    max-width: 40%;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.total-price{
    padding: 10px;
    text-align: center;
    vertical-align: middle;
    background-color: #2cbc19;
    border-radius: 5px;
    color: white;
    border: 2px solid white;
}