.cart-item-card{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}
.cart-item-img{
    width: 200px;
    height: 200px;
    background-color: white;
    border-radius: 100%;
    overflow: hidden;
}

.cart-item-img img{
    height: 50%;
    margin-top: 25%;
    margin-left: 25%;
    width: 50%;
}

.cart-item-details{
    display: flex;
    flex-direction: column;
    text-align: left;
    max-width: 60%;
    min-width: 60%;
    align-items: flex-start;
}
.quantity {
    display: flex;
    flex-direction: row;
    column-gap: 10px;
}