.add-to-cart {
    background-color: #2cbc19;
    padding: 5px;
    color: white;
    border: 0 solid #ffffff00;
    border-radius: 5px;
    font-size: calc(10px + 2vmin);
    cursor: pointer;
}

.add-to-cart-container {
    column-gap: 25px;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: row;
    cursor: pointer;
}