.header {
  padding: 10px;
  display: flex;
  position: fixed;
  top: 0;
  width: 100vw;
  justify-content: space-between;
  font-size: calc(20px + 2vimn);
  background-color: #fff;
}

.header-logo {
  width: 80px;
  height: 80px;
  overflow: hidden;
}

.header-logo img {
  margin-left: -50%;
  height: 100%;
  width: 200%;
  border-radius: 100%;
  object-fit: contain;
}

.header-categories {
  list-style-type: none;
  width: calc(100% - 300px);
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.category-item {
  padding: 2px;
  text-transform: capitalize;
}

.category-item:hover {
  cursor: pointer;
}

.category-item--selected {
  color: #2cbc19;
  text-transform: uppercase;
}

.cart-icon {
  width: 30px;
  height: 30px;
  margin: 20px 15px 0 0;
}

.cart-length {
  font-size: calc(15px + 2vmin);
  margin: -15px 50px 0 0;
  vertical-align: text-bottom;
}
.logout-button {
  position: absolute;
  right: 60px;
  top: 90vh;
  color: white;
  border: none;
  background-color: #2cbc19;
  padding: 20px;
  border-radius: 20px;
}