.parent-container{
    min-width: 100vw;
    min-height: 100vh;
    display: flex;
    justify-items: center;
    background-image: url('https://img.freepik.com/free-vector/gradient-purple-green-background_23-2150237174.jpg');
    background-repeat: no-repeat;
    background-size: cover;

}
.auth-container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    max-width: 540px;
    width: 80vw;
    flex-direction: column;
    background: linear-gradient(45deg,rgba(255, 255, 255, 0.237),rgba(255, 255, 255, 0.429));
    padding: 30px;
    border-radius: 20px;
}

.container-title{
    color: white;
    border-radius: 30px;
    text-align: left;
}

.container-title span{
    color: yellow;
}


.login-container {
    max-width: 100%;
    color: white;
}



.login-form {
    display: flex;
    flex-direction: column;
    row-gap: 30px;
}

.login-form label{
   display: flex;
   flex-direction: row;
   justify-content: space-between;
}



.login-form span{
display: none;
}

.login-form span:active{
    visibility: hidden
}

input{
    border: none;
    background-color: #ffffff36;
    padding: 6px;
    border-radius: 5px;
    width: 100%;
}

input::-webkit-input-placeholder{
    padding-left: 10px;
    color: rgb(255, 255, 255);
    font-weight: 500;
}

.login-form button{
    width: 100px;
    padding: 10px;
    border: none;
    border-radius: 30px;
    background-color: rgb(132, 0, 255);
    margin: auto;
    color: white;
}

.redirect-to-signup {
    margin-top: 30px;
    color: white;
}

.redirect-to-signup span {
    text-decoration: underline;
    color: rgb(255, 238, 0);
    padding: 2px;
    cursor: pointer;
}